import React from "react";
import { Tile } from "./Tile";
import { iconsByLink } from "./iconsByLink";
import './launch.scss';

export const Tiles = () => {
  return (
    <main className="tiles">
      {Object.entries(iconsByLink).map(([link, icon], index) => (
        <Tile icon={icon} key={index} link={link} />
      ))}
    </main>
  );
};
